@import url(https://fonts.googleapis.com/css?family=Mitr&display=swap);
.app {
  position: fixed;
  top:  50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.main{
  background-color: rgba(242, 221, 221, 0.66);
  border-radius: 25px;
  font-family: Mitr;
}

#login-form{
  background-color: rgba(242, 221, 221, 0.66);
  border-radius: 25px;
  font-family: Mitr;
}

input{
  font-family: Mitr;
  width: 100%;
  padding: 8px 10px;
  margin: 3px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

#pages{
  font-size:200%;
  color: #db2518;
  text-shadow: 2px 2px rgb(233, 158, 158);
}

#eat{
  font-size:200%;
  color: #db2518
}



.navbar{
  background-color: rgb(32, 32, 32);
}

.is-light{
  font-family: Mitr;
  background-color: white;
  color: black;
  border: 2px solid #555555;
}

.is-light:hover {
  background-color: #555555;
  color: white;
}

#process{
  font-family: Mitr;
  background-color: #555555;
